<template>
    <div>
        <div class="row">
            <div class="col-xxl-18 col-xl-16 col-lg-16 col-md-14 col-sm-24">
                <transition :enter-active-class="transitionPageIn" :leave-active-class="transitionPageOut" :duration="500" mode="out-in">
                    <router-view></router-view>
                </transition>
            </div>
            <div class="col-xxl-6 col-xl-8 col-lg-8 col-md-10 col-sm-24">
                <section-block>
                    <div class="price-block acenter">
                        <span><i class="fa fa-angle-double-right"></i>&nbsp;&nbsp;&nbsp;500 </span>
                        <button class="btn btn-success pull-right"></button>
                    </div>
                </section-block>
                <section-block title="">
                    <el-checkbox v-model="visible" style="margin-top: 10px;">   </el-checkbox>
                    <router-link to="/goods/1/edit">
                        <button class="btn btn-special-primary"> </button>
                    </router-link>
                    <button class="btn btn-danger btn-full btn-special" @click="deleteDialog = true"> </button>
                </section-block>
                <section-block title="" class="hidden-zero hidden-xs hidden-sm">
                    <div class="profile-block">
                        <img src="../../assets/images/avatar.png" alt="" class="img-circle">
                        <div class="fullname">{{fullname}}</div>
                        <div class="rating"><el-rate
                                v-model="rate"
                                disabled
                                text-color="#ff9900">
                        </el-rate></div>
                    </div>
                    <div class="block-lines">
                        <div class="block-line">
                             <span class="pull-right">{{auth.since}}</span>
                        </div>
                        <div class="block-line">
                             <span class="pull-right">{{auth.group}}</span>
                        </div>
                    </div>
                    <button class="btn btn-special-primary"> </button>
                </section-block>
            </div>
        </div>
        <el-dialog title=" " v-model="deleteDialog" size="tiny">
            <div class="question">    ?</div>
            <span slot="footer" class="dialog-footer">
                <button class="btn btn-danger btn-small"> </button>
                <button class="btn btn-primary btn-small" @click="deleteDialog = false"></button>
            </span>
        </el-dialog>
    </div>
</template>

<script>
    import sectionBlock from '../global/section.vue'
    import { mapState, mapGetters } from 'vuex'

    export default {
        name: 'goodPage',
        components: {
            sectionBlock
        },
        data() {
            return {
                rate: 3.5,
                visible: true,
                deleteDialog: false,
                transitionPageIn: 'animated05 fadeIn',
                transitionPageOut: 'animated05 fadeOut'
            }
        },
        computed: {
            ...mapState(['auth']),
            ...mapGetters(['fullname']),
        },
        methods: {

        },
    }
</script>

<style>
    .price-block {
        line-height: 35px
    }
    .price-block button {
        margin-top: 2px;
    }
    .price-block span {
        font-size: 17px;
        font-weight: 400;
        color: #3c8dbc;
        display: inline-block;
        margin-top: 2px;
    }
    .price-block span i {
        position: absolute;
        font-size: 40px;
        left: 0px;
        top: 8px;
    }
</style>


